
import React, { Component } from "react";
import {FaArrowRight} from "react-icons/fa"


export default class Contact extends Component{
    constructor(props) {
        super(props)

        this.state = {
            subj: ""
        }
    }
    handleEmailInput = event => {
        this.setState({
            subj: event.target.value
        })
    }

    handleSubmitForm = event => {
        event.preventDefault();
        if(this.state.subj.length !== 0){
            window.location ="https://mail.google.com/mail/?view=cm&fs=1&to=alhdrin97@gmail.com&su="+this.state.subj;
        }
    }

    render(){
        return (
            <div className="max-w-4xl mx-auto jutify-center py-24 mb-10 ">
                <p className="text-2xl sm:text-3xl text-gray-800 text-center font-medium px-3">
                    Interest working with me?
                </p>
            
                <div className="max-w-xl flex justify-center mx-auto mt-4">
                    <div class="inline-block relative  w-5/6 sm:w-full p-2">
                        <form onSubmit={this.handleSubmitForm.bind(this)} >
                            <input type="text"  value={this.state.subj} onChange={this.handleEmailInput.bind(this)} className="p-3 text-md w-full  sm:-mr-36 rounded-md  shadow-md focus:outline-none" placeholder="Your subject title"/>
                            <button type="submit" className="cursor-pointer absolute inset-y-0 right-0 flex items-center overflow-hidden px-3 mr-2 text-gray-700 active:outline-none">
                                <FaArrowRight />
                            </button> 
                        </form>
                        
                    </div>
                </div>
            </div>
        )
    }
   
}

