import React from "react";
import {FaReact, FaPhp, FaBootstrap} from "react-icons/fa"
import {SiJavascript, SiTailwindcss, SiJquery, SiCsharp} from "react-icons/si"

let Skill = () => (
   
    <div className="max-w-4xl mx-auto jutify-center py-4 mb-10 mt-10" id="stack">
        <p className="text-2xl sm:text-4xl text-gray-900 text-center font-medium">
            Tech I Use
        </p>
        
        <div className="flex flex-wrap justify-center pt-10">
            <div className="stack-item">
                <FaReact className="mx-auto text-3xl sm:text-5xl" color="#5ED3F3" />
                <p className="stack-item-text">React</p>
            </div>
            <div className="stack-item">
                <SiTailwindcss className="mx-auto text-3xl sm:text-5xl" color="#06B6D4" />
                <p className="stack-item-text">Tailwindcss</p>
            </div>
            <div className="stack-item">
                <SiJavascript className="mx-auto text-3xl sm:text-5xl" color="#EFD81D" />
                <p className="stack-item-text">Javascript</p>
            </div>
            <div className="stack-item">
                <SiJquery className="mx-auto text-3xl sm:text-5xl" color="#0865A7" />
                <p className="stack-item-text">JQuery</p>
            </div>
            <div className="stack-item">
                <SiCsharp className="mx-auto text-3xl sm:text-5xl text-purple-900" />
                <p className="stack-item-text">CSharp</p>
            </div>
            <div className="stack-item">
                <FaPhp className="mx-auto text-3xl sm:text-5xl" color="#777BB3" />
                <p className="stack-item-text">PHP</p>
            </div>
            <div className="stack-item">
                <FaBootstrap className="mx-auto text-3xl sm:text-5xl text-purple-700" />
                <p className="stack-item-text">Bootstrap 5</p>
            </div>
           
        </div>
    </div>
)

export default Skill;